.header__email {
    margin: 0;
    padding: 8px 24px 0;
    font-family: 'Inter', sans-serif;
    font-size: 18px;
    line-height: 22px;
    font-weight: 500;
    color: #ffffff;
}

@media screen and (max-width: 767px) {
    .header__email {
        padding: 0 8px 40px;
        font-size: 14px;
        line-height: 17px;
    }
}
